import React, { useContext, useState } from 'react';
import { AuthContext } from '../../Contexts/Auth';
import { AuthContextSuper } from '../../Contexts/SuperAuth';
import './titulo.css';

export default function Titulo({ children, nome }) {
  const { setSuperUser, superUser,superSignOut } = useContext(AuthContextSuper);
  const{signOut}=useContext(AuthContext);



  return (
    <div className='content'>
      {children}
      <span>{nome}</span>
      {superUser ? (
        <button className='btn-logout' onClick={(e)=>superSignOut()}>Logout</button>
      ):(
      <button className='btn-logout' onClick={(e)=>signOut()}>Logout</button>
      )}
    </div>
  )
}
