import React, { createContext, useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import firebase from '../services/firebaseConnection';


export const AuthContextSuper = createContext({});
export const AuthProviderSuper = ({ children }) => {


    const [superUser, setSuperUser] = useState(null);
    const [loadingAuth, setLoadingAuth] = useState(false);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        function loadingStorage() {
            const storageUser2 = localStorage.getItem('Marketing');

            if (storageUser2) {
                setSuperUser(JSON.stringify(storageUser2));

                setLoading(false);
            }
            setLoading(false);

            setLoading(false);
        }

        loadingStorage();

    }, [])


    //superUser
    async function signinSuper(email, password) {
        setLoadingAuth(true);
        await firebase.auth().signInWithEmailAndPassword(email, password)
            .then(async (value) => {
                let uid = value.user.uid;
                const superProfile = await firebase.firestore().collection('superuser')
                    .doc(uid).get();
                let superdata = {
                    uid: uid,
                    Snome: superProfile.data().nome,
                    Semail: superProfile.data().email,
                    Ssenha: superProfile.data().senha,
                    status: superProfile.data().status
                };
                setSuperUser(superdata);
                storageUser2(superdata);
                setLoadingAuth(false);
                toast.success('Olá super-user!😍')
            })
            .catch((error) => {
                console.log(error);
                setLoadingAuth(false);
                toast.error('Que pena!\n Você nao e um super-user!😢')

            })
    }


    //cadastro superUser
    async function signUpSuper(email, senha, nome, status) {
        setLoadingAuth(true);

        await firebase.auth().createUserWithEmailAndPassword(email, senha)
            .then(async (value) => {
                let uid = value.user.uid;
                await firebase.firestore().collection('superuser')
                    .doc(uid).set({
                        nome: nome,
                        senha: senha,
                        email: email,
                        status: status
                    })
                    .then(() => {
                        let superdata = {
                            uid: uid,
                            nome: nome,
                            email: value.user.email
                        };
                        setSuperUser(superdata);
                        storageUser2(superdata);
                        setLoadingAuth(false);
                        toast.success('Olá super-user!🤩')
                    })


            })
            .catch((error) => {
                console.log(error);
                setLoadingAuth(false);
                toast.error('Você nao e um super-user!\nContate o adm!😢')

            })
    }


    //localstorage
    function storageUser2(superdata) {
        localStorage.setItem('Marketing', JSON.stringify(superdata));

    }


    //superuser


    async function superSignOut() {
        await firebase.auth().signOut();
        localStorage.removeItem('Marketing');
        setSuperUser(null);
    }

    return (
        <AuthContextSuper.Provider value={{

            superSigned: !!superUser,
            loading,
            superUser,
            signUpSuper,
            signinSuper,
            superSignOut,
            loadingAuth,
            setSuperUser,
            storageUser2
        }}
        >
            {children}

        </AuthContextSuper.Provider>
    )

}

export default AuthProviderSuper