import React from 'react';
import './modal.css';

import { FiX } from 'react-icons/fi';
import { Button } from 'react-bootstrap';


export default function Modal({conteudo, close}){
  return(
    <div className="modal">
      <div className="container">
        <button className="close" onClick={ close }>
          <FiX size={23} color="#FFF" />
          Voltar
        </button>

        <div>

          <div className='image'>
            <img src={conteudo.imageUrl} alt={conteudo.produto} />
          </div>
          <h3>Detalhes do Produto</h3>

          <div className="row">
            <span>
              Nome : <i>{conteudo.produto}</i>
            </span>
          </div>

          <div className="row">
            <span>
              Descrição : <i>{conteudo.descricao}</i>
            </span>
          </div>

          <div className="row">
            <span>
              Categoria : <i>{conteudo.categoria}</i>
            </span>
            <span>
              Cadastrado em : <i>{conteudo.cadastro}</i>
            </span>
          </div>
          <a href={conteudo.link} target='_blank' rel='noopener noreferrer' ><Button variant='primary' >Comprar</Button></a>

          

          

        </div>
      </div>
    </div>
  )
}