import React, { useContext } from 'react'
import { Redirect, Route } from 'react-router-dom';
import { AuthContext } from '../Contexts/Auth';
import { AuthContextSuper } from '../Contexts/SuperAuth';

export default function RouteWrapper({
    component: Component,
    isPrivate,
    isPrivate2,
    ...rest
}) {

    const {  signed,signOut } =useContext(AuthContext);
    const { superSigned} =useContext(AuthContextSuper);
    const loading = null;

    if (loading) {
        return (
            <div></div>
        )
    }

    if (!signed && isPrivate)  {
        return <Redirect to='/' />
    }
    if(!superSigned && isPrivate2 ){
        
        return <Redirect to='/' />
    }

    return (
        <Route
            {...rest}
            render={props => (
                <Component {...props} />
            )}
        />
    )
}
