import React, { useContext } from 'react';
import { Navbar, Container, Nav, NavbarBrand } from 'react-bootstrap';
import './header.css';
import { AiOutlineUser } from 'react-icons/ai';
import { GiPoliceOfficerHead } from 'react-icons/gi'
import { Link } from 'react-router-dom';
import { AuthContext } from '../../Contexts/Auth';
import { AuthContextSuper } from '../../Contexts/SuperAuth';




export default function Header() {
    const { superSigned } = useContext(AuthContextSuper);
    const { signed } = useContext(AuthContext);

    return (
        <>
            {superSigned && (

                <Navbar bg="dark" variant="dark" expand="lg" >
                    <Container className='navBody'>
                        <Navbar.Brand ><Link to='/' className='titulo'>New Marketing </Link></Navbar.Brand>

                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="me-auto navItem">
                                <Link className='navItems' to='/' >Home</Link>
                                <Link className='navItems' to="/loginUser">Login</Link>
                                <Link className='navItems' to="/CadUser">Cadastrar</Link>

                                <Link className='navItems' to="/novoProduto">Novos Produtos</Link>


                                <Link className='navItems' to="/status">Status</Link>


                            </Nav>
                        </Navbar.Collapse>
                        {superSigned && (<NavbarBrand> < GiPoliceOfficerHead size={25} color='#FFD700' />  <span className='on'>ON</span></NavbarBrand>)}
                        {signed && (<NavbarBrand> < AiOutlineUser size={25} color='#FFD700' />  <span className='on'>ON</span></NavbarBrand>)}
                        {!signed && !superSigned && (<NavbarBrand> < AiOutlineUser size={25} color='#fff' />  <span className='off'>OFF</span></NavbarBrand>)}

                    </Container>
                </Navbar>
            )}

            {signed && !superSigned && (

                <Navbar bg="dark" variant="dark" expand="lg" >
                    <Container className='navBody'>
                        <Navbar.Brand ><Link to='/' className='titulo'>New Marketing </Link></Navbar.Brand>

                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="me-auto navItem">
                                <Link className='navItems' to='/' >Home</Link>
                                <Link className='navItems' to="/loginUser">Login</Link>
                                <Link className='navItems' to="/CadUser">Cadastrar</Link>




                            </Nav>
                        </Navbar.Collapse>

                        <NavbarBrand> < AiOutlineUser size={25} color='#FFD700' />  <span className='on'>ON</span></NavbarBrand>

                       
                    </Container>
                </Navbar>
            )}
            {!superSigned && !signed && (

                <Navbar bg="dark" variant="dark" expand="lg" >
                    <Container className='navBody'>
                        <Navbar.Brand ><Link to='/' className='titulo'>New Marketing </Link></Navbar.Brand>

                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="me-auto navItem">
                                <Link className='navItems' to='/' >Home</Link>
                                <Link className='navItems' to="/loginUser">Login</Link>
                                <Link className='navItems' to="/CadUser">Cadastrar</Link>

         
                            </Nav>
                        </Navbar.Collapse>

                        {!signed && !superSigned && (<NavbarBrand> < AiOutlineUser size={25} color='#fff' />  <span className='off'>OFF</span></NavbarBrand>)}
                        
                    </Container>
                </Navbar>
            )}
           

        </>

    )

}
