import React, { useEffect, useState } from 'react';
import Header from '../../Components/Header';
import Titulo from '../../Components/Titulo';
import { FiPlusCircle } from 'react-icons/fi';
import './setProdutos.css';
import { TiShoppingCart } from 'react-icons/ti';
import { AiOutlineClose } from 'react-icons/ai';
import firebase from '../../services/firebaseConnection';
import { toast } from 'react-toastify';
import { Link } from 'react-router-dom';
import ModalEditProduts from '../../Components/ModalEditProduts';
import { Button } from 'react-bootstrap';




export default function SetProdutos() {
     const [showModal, setShowModal] = useState(false);
    const [loadItems, setLoadItems] = useState(false);
    const [selectCategoria, setSelectCategoria] = useState('Educacao');
    const [imageProduto, setImageProduto] = useState(null);
    const [selectedProduto, setSelectedProduto] = useState('');
    const [idProduto, setIdProduto] = useState(false);
    const [filteredItems, setFilteredItems] = useState([]);

    //pesquisa
    const [pesquisar, setPesquisar] = useState([]);
    //filter
    const [listProdutos, setListProdutos] = useState([]);

    //leituras bd produtos
    useEffect(() => {
        async function loadProdutos() {
            await firebase.firestore().collection('produtos')
                .get()
                .then((snapshot) => {
                    let lista = [];
                    snapshot.forEach((doc) => {
                        lista.push({
                            id: doc.id,
                            produto: doc.data().produto,
                            categoria: doc.data().categoria,
                            imageProduto: doc.data().imageProduto,
                            imageUrl: doc.data().imageUrl,
                            preco: doc.data().preco,
                            cadastro: doc.data().cadastro,
                            descricao: doc.data().descricao,
                            link: doc.data().link,
                        })

                    })
                    if (lista.length === 0) {
                        console.log('Nenhum Produto encontrado');
                        toast.info('Nenhum Produto encontrado!😓');
                        setListProdutos([{ id: 1, produto: 'Error' }]);
                        setLoadItems(false);
                        return;
                    }
                    setListProdutos(lista);
                    setLoadItems(false);

                    if (lista !== '') {
                        loadId(lista);
                    }


                })
                .catch((error) => {
                    console.log('houve falha na leitura do banco de dados', error)
                })

        }
        loadProdutos();


    }, [listProdutos.id]);

    const lowerSelectId = selectedProduto.toLowerCase();
    useEffect(() => {
        async function filterList() {

            if (selectedProduto !== '') {
                setFilteredItems(
                    Object.values(listProdutos).filter((item) =>
                        item.id.toLowerCase().includes(lowerSelectId)))

                return;

            }


        }


        filterList()



    }, [lowerSelectId, listProdutos]);


    function loadId(lista) {

        if (selectedProduto !== '') {

            let index = lista.findIndex(item => item.id === listProdutos.id);
            setSelectedProduto(index);
            setIdProduto(true);

        } else {
            console.log('erro no id passado : ');
            setIdProduto(false);
        }


    }




    function handleFile(e) {
        if (e.target.files[0]) {
            const image = e.target.files[0];

            if (image.type === 'image/jpeg' || image.type === 'image/png') {
                setImageProduto(image);


            } else {
                alert('Envie uma imagem do tipo jpeg ou PNG');
                setImageProduto(null);
                return null;
            }

        }


    }

   
    function handleSelectProdutos(e) {
        e.preventDefault();
        setSelectedProduto(e.target.value);


    }

    function handleChangeCateg(e) {
        setSelectCategoria(e.target.value);

    }

    function toglePostModal(filteredItems) {
        setShowModal(!showModal);


    }




    return (
        <div className='container-fluid'>
            <Header />
            <div className='content'>
                <Titulo nome='Editar Produto'>
                    <FiPlusCircle size={35} />
                </Titulo>
                <div className='displayCadastroPro'>
                    <div className='cadastroPro'>
                        <div className='logoFormPro'>
                            <h2>New Marketing</h2>
                            <TiShoppingCart size={45} color='#fff' />
                        </div>
                        <form className='formCadastroPro' autoComplete='off' >
                            <label className='labelPro'>Produtos :</label>
                            <select name='Selecione uma opção' className='selectPro' value={selectedProduto} onChange={handleSelectProdutos} >

                                <option value={''}  >selecione uma opção</option>
                                {Object.values(listProdutos).map((item, index) => {
                                    return (
                                        <option key={index} value={item.id}>{item.produto}</option>
                                    )

                                })

                                }



                            </select><br />    
                            
                            {console.log(filteredItems)}
                            <div className='divButtons'>


                                {selectedProduto !== '' && Object.values(filteredItems).map((item, index) => {
                                    return (
                                        <ModalEditProduts key={index} items={item} />)
                                    })
                                }
                            

                               
                               
                                <Link to='/novoProduto'><Button variant='primary' >Sair <AiOutlineClose color='#FFF' size={25} /></Button></Link>

                            </div>

                        </form>


                    </div>


                </div>


            </div>


        </div >
    )
}
