import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import './modalEditProduts.css';
import { useHistory } from 'react-router';
import { AiOutlineClose, AiOutlineSave } from 'react-icons/ai';
import { BsTrash } from 'react-icons/bs'
import { FormGroup, FormSelect } from 'react-bootstrap';
import { toast } from 'react-toastify';
import firebase from '../../services/firebaseConnection';


export default function ModalEditProduts({ items }) {
    const [show, setShow] = useState(false);
    const history = useHistory();


    const handleShow = () => setShow(true);
    const [editProdutos, setEditProdutos] = useState(items.produto);
    const [editCategoria, setEditCategoria] = useState(items.categoria);
    const [editDescricao, setEditDescricao] = useState(items.descricao);
    const [editLink, setEditLink] = useState(items.link);
    const [editPreco, setEditPreco] = useState(items.preco);
    const [editImageProduto, setEditImageProduto] = useState('');
    const [loading, setLoading] = useState(false);
    const [delLoading, setDelLoading] = useState(false);

    function handleClose() {
        
        history.push('/novoProduto');

        setShow(false);
    }

    async function uploadImages(e) {
        e.preventDefault();
        setLoading(true);
        const uploadTask = await firebase.storage()
            .ref(`images/${editProdutos}`)
            .put(editImageProduto)
            .then(async () => {
                setLoading(false);
                console.log('Foto enviada com sucesso!');

                await firebase.storage()
                    .ref(`images`)
                    .child(editProdutos)
                    .getDownloadURL()
                    .then(async (url) => {
                        let fotoUrl = url;

                        if (items.id !== '') {
                            setLoading(true);
                            await firebase.firestore().collection('produtos')
                                .doc(items.id)
                                .update({
                                    id: items.id,
                                    categoria: editCategoria,
                                    produto: editProdutos,
                                    imageProduto: null,
                                    imageUrl: fotoUrl,
                                    preco: editPreco,
                                    cadastro: new Date(),
                                    descricao: editDescricao,
                                    link: editLink,



                                }, setLoading(true))

                                .then(() => {


                                    setEditProdutos('');
                                    setEditImageProduto('');
                                    setEditPreco('');
                                    setEditDescricao('');
                                    setEditLink('');
                                    setLoading(false);
                                    toast.info('OK!Produto editado com sucesso!👍');
                                    history.push('/novoProduto');
                                   


                                })
                                .catch((error) => {
                                    console.log(error);
                                    toast.error('Erro ao cadastrar um novo produto!🙄');

                                })
                            return;
                        }

                    })
            })



    }


    function handleImage(e) {
        if (e.target.files[0]) {
            const image = e.target.files[0];

            if (image.type === 'image/jpeg' || image.type === 'image/png') {
                setEditImageProduto(image);


            } else {
                alert('Envie uma imagem do tipo jpeg ou PNG');
                setEditImageProduto(null);
                return null;
            }

        }


    }
    function handleCategoria(e) {
        setEditCategoria(e.target.value);

    }

    async function handleDelete(e) {
        e.preventDefault();
        let confirma = window.confirm('Deseja realmente deletar esse produto?', true);
        if (confirma) {
            
        setDelLoading(true);
        const uploadTask = await firebase.storage()
            .ref(`images/${editProdutos}`)
            .delete()
            .then(async () => {
                setDelLoading(false);
                console.log('Foto deletada com sucesso!');

              

                        if (items.id !== '') {
                            setDelLoading(true);
                            await firebase.firestore().collection('produtos')
                                .doc(items.id)
                                .delete({
                                   



                                }, setDelLoading(true))

                                .then(() => {


                                    setEditProdutos('');
                                    setEditImageProduto('');
                                    setEditPreco('');
                                    setEditDescricao('');
                                    setEditLink('');
                                    setLoading(false);
                                    toast.info('OK!Produto deletado com sucesso!👍');
                                    history.push('/novoProduto')


                                })
                                .catch((error) => {
                                    console.log(error);
                                    toast.error('Erro ao cadastrar um novo produto!🙄');

                                })
                            return;
                        }

                    
            })


            
            toast.success('Produto deletado com sucesso!')
        } else {
            toast.info('operação cancelada!')
        }
    }

    return (
        <>
            <Button variant="primary" onClick={handleShow}>
                Editar Produto
            </Button>


            <Modal show={show} onHide={handleShow} >
                <Modal.Header >
                    <Modal.Title>Selecionado: {'' + items.produto}</Modal.Title>
                    <Button variant='danger' onClick={handleClose} ><AiOutlineClose color='#FFF' size={25} /></Button>
                </Modal.Header>
                <Modal.Body  >
                    <Form>
                        <Form.Group
                            className="mb-3"
                            onSubmit={uploadImages}
                            controlId="exampleForm.ControlInput1">
                            <Form.Label>Produto :</Form.Label>
                            <Form.Control
                                type="text"
                                value={editProdutos}
                                onChange={(e) => setEditProdutos(e.target.value)}
                                placeholder="nome do produto"
                                autoFocus
                            />
                            <Form.Label>Preço :</Form.Label>
                            <Form.Control
                                type="text"
                                value={editPreco}
                                onChange={(e) => setEditPreco(e.target.value)}
                                placeholder="R$ : 00,00"
                                autoFocus
                            />
                            <Form.Label>link do produto :</Form.Label>
                            <Form.Control
                                type="text"
                                value={editLink}
                                onChange={(e) => setEditLink(e.target.value)}
                                placeholder="https://www.meulink.com"
                                autoFocus
                            />
                            <Form.Label>Foto :</Form.Label>
                            <Form.Control
                                type="file"
                                accept='image/*'
                                autoFocus
                                onChange={handleImage}



                            />
                           
                        </Form.Group>
                        <FormGroup className="mb-3">

                            <Form.Label>Categoria :</Form.Label>
                            <FormSelect title='categoria'value={editCategoria} onChange={handleCategoria}>
                                <option value={''}>Selecione a nova categoria</option>
                                <option value={'educacao'}>Educação</option>
                                <option value={'beleza'}>Beleza</option>
                                <option value={'lazer'}>Lazer</option>
                                <option value={'saude'}>Saúde</option>

                            </FormSelect>
                        </FormGroup>
                        <Form.Group
                            className="mb-3"
                            controlId="exampleForm.ControlTextarea1"
                        >
                            <Form.Label>Descrição</Form.Label>
                            <Form.Control as="textarea"
                                rows={7}
                                style={{ resize: 'none' }}
                                value={editDescricao}
                                onChange={(e) => setEditDescricao(e.target.value)}
                            />
                        </Form.Group>

                    </Form>
                </Modal.Body>
                <Modal.Footer className='footer'>
                   
                    <Button variant="primary" onClick={uploadImages}>
                        {loading ?'Salvando...':'Salvar'}
                        <AiOutlineSave color='#fff' />
                    </Button>
                    <Button variant="danger" onClick={handleDelete}>
                        {delLoading ? 'Deletando...':'Deletar'}
                        <BsTrash color='#fff' />
                    </Button>
                    <Button variant="secondary" onClick={handleClose}>
                        Fechar
                        <AiOutlineClose color='#fff' />
                    </Button>
                </Modal.Footer>
            </Modal>




        </>
    );




}




